$txt-color: #404040;
$txt-hover: #e2e2e2;

html, body {
  height: 100%;
}

#app {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  font-family: 'Open Sans', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: $txt-color;
}

.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  background: #6b54e7;

  &.fixed {
    background: transparent;
  }

  .logo {
    width: 179px;
    padding: 10px;
    margin: 10px 20px;

    &.hidden {
      visibility: hidden;
    }

    svg {
      max-width: 100%;
      fill: $yellow-color;
    }
  }

  @media screen and (max-width: 768px) {
    .logo {
      margin: 0 10px;
    }

    .nav {
      top: 55px;
    }
  }
}

.nav-wrap {
  position: relative;
  padding: 10px 22px;
  text-align: right;

  .v-list__tile {
    padding: 0;
  }

  .v-btn {
    min-width: auto;
    padding: 0 10px;
  }

  @media (max-width: 768px) {
    padding: 10px 12px;
  }

  a {
    display: block;
    width: 100%;
    padding: 10px 15px;
    font-weight: bold;
    color: $txt-color;
    text-align: center;
    text-decoration: none;

    &:hover {
      background-color: $txt-hover;
    }

    &.router-link-exact-active {
      color: $primary-color;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.container {
  max-width: 1024px !important;
  padding: 16px 30px 140px !important;
  box-sizing: border-box;

  @media screen and (max-width: 768px) {
    padding-left: 20px!important;
    padding-right: 20px!important;
  }
}

.content-wrap {
  flex: 1 0 auto;
}

.footer {
  position: relative;
  z-index: 1;
  padding: 30px;
  font-size: 14px;
  line-height: 1.5;

  &:not(.white) {
    background-color: $primary-color;
    color: $white-color;

    .clouds {
      fill: $primary-color;
    }
  }

  &.white {
    background-color: $white-color;
    color: $txt-color;

    .clouds {
      fill: $white-color;
    }
  }

  .clouds {
    position: absolute;
    top: -100px;
    left: 0;
    right: 0;
    margin: auto;
  }

  .love {
    position: relative;
    top: 6px;
    width: 20px;
    height: 20px;
    margin: 0 4px;
  }

  @media screen and (max-width: 768px) {
    padding: 0 10px 20px;
    font-size: 11px;
  }
}

.home-page {
  .v-tabs__item {
    border-radius: 3px;
    /*margin-bottom: 20px;*/
  }

  .v-tabs__item--active {
    background-color: $yellow-color;
    color: $txt-color;
  }

  .v-window {
    margin-top: 20px;
  }

  .v-card__text {
    font-size: 16px;
    line-height: 1.5;
  }
}

.achievement-page {
  .v-list--three-line .v-list__tile {
    height: auto !important;
  }
}
</style>
